@font-face {
    font-family: "Belisa Plumilla Manual";
    src: url("../Fonts/belisa_plumilla.ttf") format('truetype');
}

@font-face {
    font-family: "Stint Ultra Expanded";
    src: url("../Fonts/StintUltraExpanded-Regular.ttf") format('truetype');
}

:root {
    --bg-of-div: rgba(255, 255, 255, 0.3);
    --bg-of-description: rgba(255, 255, 255, 0.25);
    --bg-of-creditsButton: rgba(255, 255, 255, 0.5);
    --bg-of-creditsButton_hover: rgba(255, 255, 255, 0.3);
    --backdrop-blur: blur(5px);
    --width-of-main: 82vw;
    --box-shadow-div: 0 0 12px black;
}

body {
    margin: 0;
}

.imageBackground {
    background-image: url("../Images/background-image.jpg");
    background-size: cover;
    background-position-y: center;
    min-height: 100vh;
    filter: brightness(0.85);
}

.containerMain {
    display: flex;
    justify-content: center;
    align-items: center;
}

.main {
    position: absolute;
    background: linear-gradient(166deg, var(--bg-of-div), transparent);
    backdrop-filter: var(--backdrop-blur);
    width: var(--width-of-main);
    height: 90vh;
    top: 5vh;
    border-radius: 20px;
    box-shadow: var(--box-shadow-div);
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: "Belisa Plumilla Manual";
    font-size: 20px;
    position: relative;
    width: 73vw;
    top: 10vh;
    left: 50%;
    transform: translateX(-50%);
}

.title h2 {
    font-size: min(40px, 7.7vw);
    color: rgb(21, 21, 21);
    margin-bottom: 0px;
}

.title h1 {
    font-size: min(85px, 11.2vw);
}

.description {
    position: absolute;
    font-family: "Stint Ultra Expanded";
    font-size: min(25px, 4.9vw);
    left: 50%;
    transform: translateX(-50%) translateY(12vh);
    padding: 15px;
    background-color: var(--bg-of-description);
    width: 60vw;
    height: 30vh;
    border-radius: 20px;
    text-align: center;
    box-shadow: var(--box-shadow-div);
}

.allPages {
    position: absolute;
    display: flex;
    align-items: center;
    width: var(--width-of-main);
    height: 40px;
    top: 1vh;
    z-index: 1;
    border-bottom: 2px solid black
}

.allPages ul {
    list-style: none;
    display: flex;
    flex-direction: row;
}

.allPages ul li {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 3px;
    background-color: var(--bg-of-creditsButton);
    border-radius: 12px;
    border: 3px solid black;
    width: var(--width-of-button, 5.3vw);
    height: 25px;
    font-size: 20px;
}

.allPages ul li:hover {
    background-color: var(--bg-of-creditsButton_hover);
    border: 2px solid black;
    cursor: pointer;
}

.allPages ul li a {
    color: black;
    width: 5vw;
    text-align: center;
    font-family: "Stint Ultra Expanded";
    font-size: 19px;
    text-decoration: none;
}

.buttonOfCalcOfDays {
    --width-of-button: 12vw;
}

.startNowButton {
    margin-top: 45px;
    padding: 12px;
    background-color: var(--bg-of-creditsButton);
    border-radius: 12px;
    border: 2px solid black;
    font-size: 25px;
    box-shadow: 3px 4px 9px #2a2722;
}

.startNowButton:hover {
    cursor: pointer;
}


.section {
    position: absolute;
    font-family: "Stint Ultra Expanded";
    font-size: min(25px, 5vw);
    left: 50%;
    transform: translateX(-50%) translateY(12vh);
    padding: 15px;
    background-color: var(--bg-of-description);
    width: 60vw;
    height: 64vh;
    border-radius: 20px;
    text-align: center;
    box-shadow: var(--box-shadow-div);
}

.titleSection {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: "Belisa Plumilla Manual";
    font-size: 20px;
    position: relative;
    width: 73vw;
    height: 8vh;
    top: 10vh;
    left: 50%;
    transform: translateX(-50%);
}

.titleSection h1 {
    font-size: min(55px, 9vw);
}

#daysPassed_SearchBar {
    --box-shadow-div: 0 0 6px black;
    border: none;
    outline: none;
    width: 12vw;
    border-radius: 20px;
    padding: 10px;
    box-shadow: var(--box-shadow-div);
}

#daysPassed_SearchBar:hover, #daysPassed_SearchBar:focus {
    background-color: rgba(255, 255, 255, 0.8);
    cursor: pointer;
}

.daysPassed_Button {
    --box-shadow-div: 0 0 6px black;
    border: none;
    position: relative;
    width: 4vw;
    height: 35px;
    border-radius: 10px;
    padding: 5px;
    box-shadow: var(--box-shadow-div);
}

.daysPassed_Button i {
    position: absolute;
    font-size: 20px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.daysPassed_Button:hover {
    background-color: rgba(255, 255, 255, 0.8);
    cursor: pointer;
}

.dataText {
    position: relative;
    top: 10vh;
}

.daysPassed_Title {
    margin-bottom: 10px;
}

.daysPassed_Description {
    margin-top: 10px;
}

.resultsTable_Wrapper {
    --width-of-results-table: 55vw;
    position: absolute;
    left: 50%;
    top: 10vh;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}

.resultsTable {
    overflow-y: auto;
    background-color: var(--bg-of-description);
    width: var(--width-of-results-table);
    border-radius: 20px;
}

/* width */
.resultsTable::-webkit-scrollbar {
    width: 18px;
}
  
/* Track */
.resultsTable::-webkit-scrollbar-track {
    border-radius: 10px;
}
   
/* Handle */
.resultsTable::-webkit-scrollbar-thumb {
    background: var(--bg-of-creditsButton); 
    border-radius: 20px;
}
  
/* Handle on hover */
.resultsTable::-webkit-scrollbar-thumb:hover {
    background: var(--bg-of-div); 
}

.seasonsDiv {
    margin-top: 4vh;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.creditsCard_Wrapper {
    --width: 50vw;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    overflow-x: auto;
    position: relative;
    border-radius: 20px;
    width: var(--width);
    background-color: var(--bg-of-div);
    box-shadow: var(--box-shadow-div);
}

/* width */
.creditsCard_Wrapper::-webkit-scrollbar {
    width: 18px;
}
   
/* Handle */
.creditsCard_Wrapper::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.7); 
    border-radius: 20px;
    border: 1px solid black;
}
  
/* Handle on hover */
.creditsCard_Wrapper::-webkit-scrollbar-thumb:hover {
    background: var(--bg-of-creditsButton); 
}

.creditsCard {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: var(--width);
}

.creditsCard img {
    margin-bottom: 30px;
    box-shadow: var(--box-shadow-div);
}

.creditsCard .links {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: var(--box-shadow-div);
}

.creditsCard a {
    width: 12vw;
    background-color: var(--bg-of-creditsButton);
    padding: 10px;
    text-decoration: none;
    color: black;
}

.creditsCard a:hover {
    background-color: var(--bg-of-creditsButton_hover);
}

/* Mobile */
@media only screen and (max-device-width: 640px) {
    
    .imageBackground {
        background-position: bottom;
    }
    
    .startNowButton {
        margin-top: 0;
    }

    .main {
        --width-of-main: 90vw;
    }

    .description {
        width: 74vw;
        height: 37vh;
    }

    .allPages {
        overflow-x: auto;
        overflow-y: hidden;
    }

    .allPages ul {
        padding-left: 8px;
    }

    .allPages ul li {
        width: var(--width-of-button, 24.3vw);
    }

    .allPages ul li a {
        width: 23vw;
    }

    .buttonOfCalcOfDays {
        --width-of-button: 52.3vw;
    }

    .section {
        height: 61vh;
    }

    #daysPassed_SearchBar {
        width: 36vw;
    }

    .daysPassed_Button {
        width: 10vw;
    }

    .resultsTable {
        --width-of-results-table: 70vw;
    }

}